<template>
  <div id="app">
  <div class="page" v-if="$route.meta.keepAlive">
    <keep-alive>
      <router-view/>
    </keep-alive>
  </div>
  <div class="page" v-else>
    <router-view/>
  </div>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



