import Vue from 'vue'
import Router from 'vue-router'
const HelloWorld = resolve => {require.ensure(['@/components/HelloWorld'], () => {resolve(require('@/components/HelloWorld'))})}

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      meta:{
      	title:"测试页面",
      	keepAlive:true, 	//是否开启缓存
      }
    }
  ]
})



